import React  from 'react';



const Vefrit = () =>

  <div>
      <h1>Plgur - Vefrit</h1>
      <div>
            <p>
                  Landbnaarsafn slands stofnar til vefritsins Plgur. Vefritinu er tla a vera vettvangur til birtingar greina um slenska bnaarsgu ea vifangsefni tengt henni. Vefriti  a varveita og mila lsandi, greinandi, frandi og skemmtandi frleik um einkenni, run og stu slensks landbnaar  msum tmum og  msum svum.
                  Vefriti kemur t eftir efnum og stum. tkoma ns efnis ritsins verur kynnt hverju sinni  frttadlki heimasu safnsins www.landbunadarsafn.is. Efni ritsins verur eingngu birt ar. Eftir stum og v, sem vi , er  gert r fyrir a efni ess birtist  ru formi, t.d. prenta.
                  hersla er lg  a hfundar og notendur efnis vefritsins viri viurkenndar og stafestar reglur um hfundarrtt og mefer alls efnis  ritinu. Landbnaarsafn skilur sr rtt til ess a framfylgja eim me hef- og lagabundnum htti.
            </p>
            <p>
                  Vefritinu verur skipt  tvr deildir, A og B:
            </p>
            <p>
                  A-deild:  greinar byggar  rannsknum hfunda og mega teljast sjlfst vibt vi nverandi ekkingu. Varandi frgang greina er bent  almennar reglur um ritun og frgang sem kenndar eru  fyrstu rum hsklanms. Til fullkomnunar m  benda  reglur tmaritsins Saga sem veruga fyrirmynd, sj www.sogufelag.is/timarit
                  /timarit_ritreglur.html  Greinar vera lesnar yfir af kunnttumnnum (ritrndar) ur en birtar vera. Me vsun til lits ritrnenda skilur ritstjri sr rtt til ess a hafna greinum ef svo ber undir.                  
            </p>
            <p>
                  B-deild:  efni almenns elis, frsagnir og frleikur um afmrku vifangsefni n ess  a teljast lauslegt spjall ea samtningur. Efni getur m.a. veri ritaar greinar, skyggnusyrpur (PPT-skrr) ofl.
            </p>

            <p>
                  eir, sem efni eiga  frum snum, eru hvattir til ess a senda a til birtingar. Ritstjri skilur sr rtt til breytinga  formi og framsetningu efnis  samri vi hfund/hfunda.
            </p>

            <p>
                  a er megin stefna vefritsins Plgur a bera fram efni sem tla m a hfi til hins hugasama og upplsta almennings og bti honum frleik og skemmtan, auk ess a fora fr gltun og gleymsku efni sem varar sgu og run landbnaar  slandi.
                  byrgarmaur og ritstjri vefritsins verur um sinn Bjarni Gumundsson  Hvanneyri.
            </p>
      </div>

  </div>





export default Vefrit;



