import React  from 'react';

const UmSafnid = (props) =>
  <div>
    <h1> Landbnaarsafn slands </h1>
    <p>
      er sjlfseignarstofnun sem formlega var stofna 14. febrar 2007. Stofnailar eru Landbnaarhskli slands (Lbh), sveitarflagi Borgarbygg og Bndasamtk slands (B).  stjrn safnsins eiga sti, auk fulltra urnefndra stofnana, jminjavrur ea fulltri hans og fulltri tilnefndur af landbnaarrherra.

       nverandi stjrn safnsins sitja, skipair hausti 2014:

      Bjrn orsteinsson, rektor fltr. Lbh (form), vm: Ragnhildur Helga Jnsdttir.
      Ragnar Frank Kristjnsson, fltr. Borgarbyggar, vm: Helgi Hauksson.
      Haraldur Benediktsson, fltr. Bndasamtaka sl., vm: Tjrvi Bjarnason.
      Arna Bjrk Bjarnadttir, fltr. landbnaarrherra, vm: Nels rni Lund.
      Lilja rnadttir, fltr. jminjavarar, vm: gst Georgsson.
    Landbnaarsafn slands er  Hvanneyri  Borgarfiri. Verkefnisstjri ess er Ragnhildur Helga Jnsdttir. </p>
    <h1> Hlutverk Landbnaarsafns </h1>
    <p>  stofnsamykktum safnsins segir svo um hlutverk ess:

      Hlutverk Landbnaarsafns slands ses. er a gera skil sgu og run slensks landbnaar me v a varveita ggn, gripi og arar minjar um hana, og annast rannsknir og frslu um vifangsefni.

      Landbnaarsafn slands ses. leggur srstaka herslu  sgu landbnaarins fr byrjun tknialdar  landbnai me rannsknum sem og almennri kynningu  sgu hans og stu og hvers konar milun hennar, mist eitt sr ea  samvinnu vi aila sem til hennar eru fsir.

       starfi snu skal Landbnaarsafn sland ses. hafa samr og samvinnu vi  jminjasafn slands og ara opinbera aila er annast minjavernd og vrslu  landinu, .m.t. byggasfn.
    </p>

    <h1>Safn  gmlum grunni </h1>
    <p> Landbnaarsafn slands er byggt  grunni Bvlasafnsins sem starfa hafi  Hvanneyri um langt rabil. Bvlasafni rakti sgu sna til rsins 1940. Samkvmt lgum nr. 64/1940 um rannsknir  gu landbnaarins skyldi  komi upp safni af landbnaarverkfrum vi Bndasklann  Hvanneyri.

      Lti var lengi vel unnt a gera sakir fjrskorts. Gumundur Jnsson sklastjri bjargai  msum verkfrum og hlt yfir eim hlfiskildi.

      Bnaarflag slands gaf safninu vermta gripi fr lafsdalssklanum og fr bi Thors Jensen  Korplfsstum og fleirum brust einnig mis verkfri. essir gripir eru meal merkustu gripa safnsins  dag.
    </p>
    <h1> Safni opna </h1>
    <p>
       runum 1976-1980 var allmiki unni a endurreisn og eflingu verkfrasafnsins. Til st a risa hs yfir a en f skorti til framkvmda.

      Bvlasafni var fyrst opna almenningi sumari 1987,  smum stl .  hfu starfsmenn Btknideildar Rannsknastofnunar landbnaarins gert upp nokkrar vlar og tbin hafi veri dltil geymslu- og sningarastaa, sem san hefur veri aukin og btt.

      Sning safnsins var fyrst  Verkfrahsinu  Hvanneyri, sem lengi hsti bvlaprfanir Verkfranefndar og sar Btknideildar Rala, svo og vlfrikennslustofu Bndasklans og fleira (sj uppdrtt hr a ofan).

      ri 2007 var safninu breytt  ann veg a myndu var sjlfseignarstofnun um a eins og fyrr sagi. Um lei var nafni safnsins breytt og heitir a san Landbnaarsafn slands.

      Ssumars 2014 var safni flutt  Halldrsfjs  Hvanneyri, sgumerka byggingu, sem reist var  runum 1928-29. ann 2. oktber 2014 var grunnsning safnsins opnu ar og einnig teki  notkun nokku geymslurmi ar  hsinu, auk fjlnotasalar  sk. Suurhlu.

      Sustu 16 rin hefur safni veri opi um rsins hring, og er  mti gestum teki eftir rfum og pntun. Sumarmnuina jn-gst hefur safni veri opi daglega. Gestafjldi  hverju ri hefur veri um fimm sund manns.

       hefur safni  haldi ti heimasu fr rinu 2000, hn er n  www.landbunadarsafn.is

    </p>
    <h1> Veitingar  Gahandverk </h1>
    <p> Me fyrirvara m panta veitingar  matstofu Landbnaarhskla slands  Hvanneyri (s. 433 5000).  er veri a koma af sta kaffi hsi  Skemmunni  Hvanneyri, elsta hsi staarins, en a var byggt ri 1896.

      Ullarseli (www.ull.is) er  mttkurmi Landbnaarsafns slands; ar fanlegt fnasta handverk hrasins  og lklega landsins, einkum r ull og rum slenskum hrefnum.

      G veitingahs og hressingarsklar eru  nsta ngrenni safnsins: Fossatn (10 km) og Landnmssetri, Geirabakar og Htel Hamar  og vi Borgarnes. (12-13 km).

       minnum vi lka  Laxveii- og sgusafni  Ferjukoti, sem aeins er  6 km fjarlg fr Hvanneyri.
    </p>

    <h1>Nnari upplsingar um Landbnaarsafn slands </h1>

    <p>Nnari upplsingar um Landbnaarsafn eru veittar  sma 844 7740.

    byrgarmaur safnsins er Ragnhildur Helga Jnsdttir  GSM 844 7740, netfang ragnhildurhj@lbhi.is </p>
  </div>


export default UmSafnid;



