import React from 'react';
import '../../style/myStyles/_default.scss';
import  Icons from "../../img/sprites.svg";
import Portal from '../Portal';

const Opnunartimi = () =>
  <div className="default">
      
      <Portal>
                  <div className="post--component__title">
                        <h1>
                              Opnunartmi
                        </h1>
                  </div>
      </Portal>
    <div className="default--paragraph">
            <div className="default--paragraph--block">
                  <svg>
                        <use xlinkHref={`${Icons}#icon-clock`}></use>
                  </svg>
                  <span> vetur var safni opi fimmtudaga, fstudaga og laugardaga  sama tma og Ullarseli, sem er  anddyri safnsins. Landbnaarsafn slands er opi reglulega mnuina jn, jl og gst, daglega kl. 11-17.  rum tmum er safni opi eftir rfum.</span>
            </div>
            <div className="default--paragraph--block">
                  <svg>
                        <use xlinkHref={`${Icons}#icon-phone`}></use>
                  </svg>
                  <span>
                        Vinsamlegast hafi samband  sma 844 7740; einnig m hafa samband vi skiptibor Landbnaarhsklans,  sma 433 5000. Hpum er veitt leisgn um safni, s ess ska. 
                  </span>
            </div>
            <div className="default--paragraph--block">
                  <svg>
                        <use xlinkHref={`${Icons}#icon-credit-card`}></use>
                  </svg>
                  <span>
                        Almennur agangseyrir a safninu er: 1.200 kr. fyrir fullorna (og  hpum sem srstk leisgn er veitt); 1000 kr. fyrir ryrkja og eldri borgara keypis er fyrir 14 ra og yngri  fylgd fullorinna.
                  </span>
            </div>
            <div className="default--paragraph--block">
                  <svg>
                        <use xlinkHref={`${Icons}#icon-info`}></use>
                  </svg>
                  <span>
                  Einnig er boi upp  stutta kynningu  Hvanneyrarsta og starfinu ar m.a. me heimskn  Hvanneyrarkirkju, eina fallegustu kirkju landsins og rstuttri gngufer um Gamla sklastainn (egar veur leyfir). skilegt er a panta slka kynningu me fyrirvara (s. 844 77 40). Minnt er  Skemmuna kaffihs  nsta ngrenni safnsins, sem er opi daglega  sumar fr kl. 13.30.
                  </span>
            </div>
      </div>
  </div>


export default Opnunartimi;



