import React  from 'react';

const Velunnarar = () =>

  <div>
    <h1> Velunnarar </h1>
    <ul>
      <li>Landbnaarhskli slands</li>
      <li>Jrvi hf, Hvanneyri</li>
      <li>Ullarseli</li>
      <li>Safnahs / Byggasafn </li>
      <li>Borgarbygg </li>
      <li>Vrnet </li>
      <li>Furblandan hf</li>
      <li>Rarik</li>
      <li>Nepal</li>
      <li>runn Edda</li>
      <li>Erlendur Sigursson og</li>
      <li>nefndur fjldi bnda og annarra einstaklinga</li>
      
    </ul>


  </div>


export default Velunnarar;



