import React from 'react';
const Mjolkurskolinn = () =>
  <div>
    <h1> Mjlkursklinn </h1>
    <p>
      Hr  eftir fer nemendatal Mjlkursklans  Hvanneyri og Hvtrvllum. Heimildir um nemendur sklans eru mjg sundurlausar. Nfn nemenda voru jafnan birt  Bnaarriti me skrslum um sklann, en svo virist sem nokkur nfn vanti ar. nkvmni getur lka gtt  bja- og sslunfnum.  aftasta dlki tflunnar er nmstminn sem vikomandi nemandi dvaldi  sklanum. Lra gsta lafsdttir geri fyrstu tgfu skrrinnar og birti  BA-ritger sinni um Mjlkursklann og rjmabstrur 1988. Hr hefur skrin veri endurskou og aukin nokku  grundvelli heimilda sem sar hafa komi  ljs.

      Sj nnar bkina Konur breyttu bhttum  Saga Mjlkursklans  Hvanneyri og Hvtrvllum

      Nemendatal Mjolkurskolans er birt hr bi til frleiks en ekki sur til ess a leita eftir rttari og tarlegri upplsingum um nemendur Mjlkursklans. Hafir  slkar upplsingar bi g ig vinsamlegast a hafa samband vi mig, Bjarna
      Gumundsson  Hvanneyri, s. 894 6368 ea bjarnig@lbhi.is

      annig verur reynt a betrumbta nemendatali!

    </p>

  </div>


export default Mjolkurskolinn;



