import React  from 'react';
import '../../style/myStyles/_stuttmyndir.scss';

const Stuttmyndir = () =>

  <div className="stuttmyndir">

      <div className="stuttmyndir--card">
            <h1>Fordson</h1>
            <iframe  src="https://www.youtube.com/embed/8j0mK2mxKr0" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
            <p>Bjarni Gumundsson, verkefnisstjri Landbnaarsafns slands, segir fr forvera drttarvlanna.</p>
      </div>
      <div className="stuttmyndir--card">
            <h1>Skilvindur</h1>
            <iframe  src="https://www.youtube.com/embed/gMamAvZk_QE" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
            <p>Bjarni Gumundsson, verkefnisstjri vi Landbnaarsafn slands, segir skilvindum og mikilvgi eirra  slenskum heimilum  19.- og 20. ldinni.</p>
      </div>
  </div>





export default Stuttmyndir;



